body,
html {
  background: #E8ECF0;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  font-family: Arial, 'Hiragino Sans GB', 冬青黑, 'Microsoft YaHei', 微软雅黑, SimSun, 宋体, Helvetica, Tahoma, 'Arial sans-serif';
  -webkit-font-smoothing: antialiased;
}

.right_side {
  position: absolute;
  top: 9.375rem;
  bottom: 1.25rem;
  right: 1.25rem;
  left: 5.25rem;
  border-radius: 0.625rem;
  background: #fff;
  min-width: 62.5rem;
  /*min-height: 700px;*/
  overflow: scroll;
  overflow-x: hidden;
}


